import "core-js/modules/web.dom-exception.stack.js";
import Markdown from "vue3-markdown-it";
import axios from "axios";
const getReadme = url => {
  return axios.create()({
    url,
    method: "Get"
  });
};
const data = reactive({
  content: "",
  url: ""
});
export default defineComponent({
  components: {
    Markdown
  },
  setup() {
    onMounted(() => {
      const meta = useRoute().meta;
      const repository = meta.repository;
      try {
        getReadme("https://api.github.com/repos/" + repository + "/readme").then(res => {
          data.content = decodeURIComponent(Array.prototype.map.call(atob(res.data.content), function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          }).join(""));
          data.url = "https://github.com/" + repository;
        });
      } catch (e) {
        console.log(e);
      }
    });
    return {
      data
    };
  }
});